import React from 'react';
import ReactDOM from 'react-dom';
import {Tile} from './tile';
import {SearchTools} from './search-tools';
import {Button} from './button.js';
import image from '../images/chinaGuineo.jpg';
import logo from '../images/bohiologo.png';
import {theme} from './main';
import styles from './styles/checkout.module.css'


export const ChangeableInput = (props) => {
  return (
    <div className="container">
      <div className="current">
      </div>
      <div className="modify">
      </div>
    </div>
  );
}

const PaymentForm = (props) => {
  return(
    <div className="paymentFormCont">
      <div>

      </div>
    </div>
  );
}

export class Checkout extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      subView : 'products',
      view : 'gallery'
    }
    this.handlePaymentClick = this.handlePaymentClick.bind(this);
  }

  handlePaymentClick() {
    this.setState({subView : 'checkout'})
  }

  render () {
    var messageDisplay = 'flex';
    var messageNotDisplay = 'none';
    let data = this.props.productsChosen;
    if (data.length != 0) {
      messageDisplay = "none";
      messageNotDisplay = "block";
    }

    let message = {
      display : messageDisplay,
      margin : 'auto',
      flexDirection : 'column'
    }

    let checkoutStyle = {
      display : 'flex',
      flexDirection : 'column',
      justifyContent : 'space-between',
      overflowY : 'none',
      height : '100%',
      width : '100%',
    }

    let productsStyle = {
      display : 'flex',
      height : '80%',
      flexDirection : 'row',
      justifyContent : 'center',
      overflowY : 'scroll',
      width : '100%',
    }

    let functionsStyle = {
      height : '200px',
      backgroundColor : '#4cb944',
      display : 'flex',
      flexDirection : 'row',
      justifyContent : 'center',
      //alignItems : 'center',
      //borderTop : '3px solid #4cb944',
    }

    let functionsBox = {
      width : '33%',
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'space-between'
    }

    let button = {
      //marginTop : '30px'
      fontSize : '100%'
    }

    let visible = {
      display : messageNotDisplay,
    }

    let orderTotal = 0;
    let content = data.map((info, index) => {
      let total = info.price * info.quantity;
      orderTotal = orderTotal + total;
      if (this.state.view === 'gallery') {
      return (
        <Tile
          id={index}
          onAdd={(e) => this.props.onRemove(e)}
          type="checkout"
          title={info.title}
          image={info.image}
          textColor='white'
          total={total}
          price={info.price}
          unit="each"
          width="200px"
          orderQty={info.quantity}
          />
      );
    }

    else {
      return (
        <tbody>
          <tr>
          <td>{info.title}</td>
          <td>{'$' + info.price}</td>
          <td>{info.quantity}</td>
          <td>
            <img className={styles.tableImage} src={data.image}/>
            {info.supplier}
          </td>
          <td>{total}</td>
          <td id="tableAdd">
            <Button
              id={index}
              className="table"
              color={theme.blue}
              text={'-'}
              onClick={(e) => this.props.onRemove(e)}
              size="small"
              type="add"
              />
          </td>
        </tr>
      </tbody>
      );
    }
  });

    let table =
    ( <table style={{display : messageNotDisplay}}>
      <thead>
        <tr>
          <th>
            PRODUCT
          </th>
          <th>
            PRICE
          </th>
          <th>
            QUANTITY
          </th>
          <th>
            SUPPLIER
          </th>
          <th>
            SUBTOTAL
          </th>
          <th>
            REMOVE
          </th>
        </tr>
      </thead>
        {content}
    </table>
  );

    const checkoutProducts = {
      content : this.state.view === "gallery" ? content : table,
      functions : (
        <div style={{
            display:'flex',
            flexDirection:'row',
            width : '100%',
            justifyContent : 'space-between',
            alignItems : 'center',
          }}>
          <div style={button}>
            <button onClick={() => this.props.onAddMore()}>
              ADD MORE PRODUCTS
            </button>
          </div>
          <div style={button}>
            <button  style={visible} onClick={() => this.props.onSubmitOrder()}>
              SAVE ORDER
            </button>
          </div>
          <div style={{
              fontSize : '100%',
              fontWeight : 'bold',
              color: 'white'
            }}>
            {"ORDER TOTAL: $" + String(orderTotal)}
          </div>
        </div>
      )
    }
    const checkoutPayment = {
      content : <PaymentForm/>,
    functions : (
      <div>
        <div style={button}>
          <button onClick={() => this.props.onSubmitOrder()}>
            SAVE ORDER
          </button>
        </div>
      </div>
    )
  }

  const subview = this.state.subView === 'products';
  return (
    <div className='Checkout' style={checkoutStyle}>
      <SearchTools
        onTableClick={() => this.setState({view : 'table'})}
        onGalleryClick={() => this.setState({view : 'gallery'})
      }
        />
      <div style={message}>
        <img style={{filter : 'opacity(50%)'}} alt="logo" src={logo}/>
        {"No new products have been added"}
      </div>
      <div className='Products' style={productsStyle}>
        <div style={{
            width: '60%',
            display : 'flex',
            flexDirection : 'row',
            flexWrap : 'wrap'
          }}>
        {checkoutProducts.content}
      </div>
      </div>
      <div className='Functions' style={functionsStyle}>
        <div style={functionsBox}>
          {subview ? checkoutProducts.functions : checkoutPayment.functions}
        </div>
      </div>
    </div>
  );
}
}
