import {Orders} from './orders';
import {Settings} from './settings';
import {Logout} from './logout';
import logo from '../images/bohiologo.png';
import {theme} from './main';
import styles from './styles/view.module.css';
import {catdata} from '../fakeDB.js'

export class View extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      snackbarIsShowing : false,
      snackbarMessage : "",
      catalogType : 'product',
      productFilter : 'false',
      catalogData : catdata,
      productsChosen : [],
      chosenSupplier : "",
      amountChosen : 0,
      orderTotal : "",
      trigger : false
    }
    this.handleAddToCart = this.handleAddToCart.bind(this);
    this.handleRemove = this.handleRemove.bind(this);
    //this.handleSupplierClick = this.handleSupplierClick.bind(this);
    this.handleSaveOrder = this.handleSaveOrder.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
  }

  componentDidMount() {
    fetch("http://localhost:8000/company/")
    .then((res) => res.json())
    .then((data) => console.log(data));
  }

  /*handleSupplierClick(e) {
    let type = e.target.className;
    let title = e.target.id; //chosen supplier name
    if (type === 'supplier') {
      //get array of products from chosen supplier
      fetch("./products/")
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          catalogData: data,
          catalogType : 'product',
          productFilter : true,
          chosenSupplier : title
        })
      }
      );
    }
  }*/

  handleAddToCart(e) {
    const i = e.target.id;
    let quantity = getQuantity(e);
    //input validation
    if (!quantity) return(alert("you must specify a quantity"));
    if (isNaN(quantity)) return (alert("quantity must be numeric"))
    let chosen = JSON.parse(JSON.stringify(this.state.catalogData[i]));
    chosen.quantity = quantity;
    let currentOrder = this.state.productsChosen;
    this.setState({
      snackbarMessage : chosen.title + "(" + quantity + ") has been added",
      snackbarIsShowing : true,
      productsChosen : currentOrder.concat([chosen])
    });
    setTimeout(() => {this.setState({snackbarIsShowing : false});}, 3000);
  }

  handleRemove(e) {
    let i = e.target.id;
    let products = this.state.productsChosen
    products.splice(i,1);
    this.setState({
      productsChosen : products
    });
  }

  handleSaveOrder () {
    //send complete order data
    var date = new Date();
    const order = {
      "id" : "random number",
      "products" : this.state.productsChosen,
      "price" : this.state.orderTotal,
      "buyer" : this.state.userInfo,
      "seller" : this.state.chosenSupplier,
      "date" : date,
    }
    const init = {
      method : "POST",
      body : JSON.stringify(order)
    }
    fetch("./upstream-orders/", init)
    .then((res) => {})
    .then();
    alert("Order Saved. Go to 'ORDERS' for details");
    this.setState({ productsChosen : [] });
  }

  handleFilterChange (e) {
    const val = e.target.value;
    const filterData = {
      "price" : val
      //rest of the filter data
    }
    const init = {
      method : "POST",
      body : JSON.stringify(filterData)
    }
    fetch("./filter/")
    .then((res) => res.json())
    .then((data) => {/*update catalog*/});
  }

  render(){

    let snackbarStyle = {
      display : this.state.snackbarIsShowing ? 'flex' : 'none'
    }

    let viewStyle = {
      width : '100%',
      height : 'inherit',
      overflowY : 'scroll',
    }

    if (this.props.content === 'CATALOG') {
      return (
        <div className={styles.view}>
          <Catalog
            snackbar={this.state.snackbarIsShowing}
            catalogType={this.state.catalogType}
            productFilter={this.state.productFilter}
            onFilterChange={(e) => this.handleFilterChange(e)}
            onAdd={(e) => this.handleAddToCart(e)}
            onSupplierClick={(e) => this.handleSupplierClick(e)}
            catalogData={this.state.catalogData}/>
          <div className={styles.snackbarCont}>
            <div className={styles.snackbar} style={snackbarStyle}>
              <span>
                {this.state.snackbarMessage}
              </span>
              <button
                onClick={() => this.setState({snackbarIsShowing : false})}>
                ok </button>
              <button onClick={() => this.props.onViewOrder()}> view pre-order </button>
            </div>
          </div>
          </div>
      );
    }

    if (this.props.content === 'CHECKOUT') {
      return (
        <div style={viewStyle}>
          <Checkout
            onAddMore={() => this.props.onAddMore()}
            onRemove={(e) => this.handleRemove(e)}
            productsChosen={this.state.productsChosen}
            onSubmitOrder={() => this.handleSaveOrder()}/>
        </div>
      );
    }

    if (this.props.content === 'ORDERS') {
      return (
        <div style={viewStyle}>
          <Orders onSubmit={(e) => this.props.onSubmit(e)}/>
        </div>
      );
    }

    if (this.props.content === 'SETTINGS') {
      return (
        <Settings />
      );
    }
  }
}

//helper functions
function getQuantityFromTile(e) {
  return (e.target.parentNode.previousSibling.childNodes[1].value);
}

function getQuantityFromTable(e) {
  return (e.target.previousSibling.value);
}

function getQuantity(e) {
  var quantity = "";
  const type = e.target.parentNode.id;
  if (type === "galleryAdd") quantity = getQuantityFromTile(e);
  if (type === "tableAdd") quantity = getQuantityFromTable(e);
  return (quantity);
}
