import React from 'react';
import ReactDOM from 'react-dom';
import {Searchbar} from './search-bar';
import {Slider} from './slider';
import {theme} from './main';
import {Checkbox}  from './checkbox';
import {InputField} from './input-field';
import styles from './styles/search-tools.module.css'
import gallery from '../images/gallery.png';
import table from '../images/table.png';


export class Filter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value : 33
    }
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange (event) {
    this.setState({value : event.target.value});
  }

  render () {
    return (
      <div className={styles.filterArea}>
        <div className="scalingBar">
          <Slider
            min={0}
            max={100}
            val={this.state.value}
            onChange={(e) => this.handleChange(e)}>
          </Slider>
        </div>
        <div className={styles.filters}>
          <div className={styles.dropdown}>Sort
            <select>
              <option>lowest to highest</option>
              <option>highest to lowest</option>
            </select>
          </div>
          <div className={styles.dropdown}>
            Supplier
            <select name="Suppliers">
            <option value="Agroproduce">Agroproduce</option>
            <option value="Carribean Produce">Carribean Produce</option>
            <option value="Drouyn Co.">Drouyn Co.</option>
            <option value="El Viandon">El Viandon</option>
            <option value="Hill Bros.">Hill Bros.</option>
          </select>
        </div>
        <div className={styles.dropdown}>
          Unit
          <select name="Suppliers">
            <option value="lb">lb</option>
            <option value="ea">ea</option>
          </select>
        </div>
      </div>
    </div>
  );
}
}

export class SearchTools extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filterIsShowing : false,
    }
    this.handleFilter = this.handleFilter.bind(this);
  }

  handleFilter() {
    this.setState({filterIsShowing : !this.state.filterIsShowing});
  }

  render() {
    let searchToolsStyle = {
      width:'100%',
      display : 'flex',
      flexDirection : 'row',
      justifyContent : 'center',
      alignItems : 'center',
    }

    let filterStyle = {
      width : '100%',
      display : this.state.filterIsShowing ? 'flex' : 'none',
      flexDirection : 'row',
      justifyContent : 'center',
      alignItems : 'center',
      marginBottom : '20px',
    }

    let searchArea = {
      display : 'flex',
      height : 'auto',
      flexDirection : 'column',
      justifyContent : 'center',
      alignItems : 'center',
      //marginBottom : '30px',
      //boxShadow : '5px 0px 5px lightgrey',
      background : 'white'//'lightgrey''rgba(41, 240, 26, 0.5)'
    }

    const supplierFilter = (
      <Checkbox
        onChange={(e) => this.props.onFilterChange(e)}
        value="Tracking Enabled"/>
    );

    const productFilter = <Filter/>;
    return (
      <div style={searchArea}>
        <div className={styles.search}>
          <div style={{
              marginTop:'20px',
              marginBottom:'0px',
              marginLeft : '0px',
            }}>
            <Searchbar id="search"
              onChange={(e) => this.props.onFilterChange(e)}
              width="93%"
              height="50px"/>
          </div>
          <div className={styles.views}>
            <div className={styles.view}
              onClick={() => this.props.onGalleryClick()}>
              <img alt="gallery" src={gallery}/>
            </div>
            <div className={styles.view}
              onClick={() => this.props.onTableClick()}>
              <img alt="table" src={table}/>
            </div>
          </div>
          <div style={{
              marginLeft : '16px',
              marginTop : '5px',
            }}>
            <button
              onClick={() => this.handleFilter()}>
              FILTER
            </button>
          </div>
        </div>
        <div style={filterStyle}>
          {
            this.props.productFilter ?
            productFilter : supplierFilter
          }
        </div>
      </div>
    );
  }
}
