import React from 'react';
import ReactDOM from 'react-dom';
import {Button} from './button';
import {Tile} from './tile';
import {Slider} from './slider';
import {Checkbox} from './checkbox';
import {SearchTools} from './search-tools';
import logo from '../images/cp1.png';
import {theme} from './main'
import styles from './styles/catalog.module.css';

export class Catalog extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      view : 'gallery',
    }
  }

  render () {
    let data = this.props.catalogData;
    //jsx for tile view of catalog
    let catalogTiles = data.map((data, index) => {
      return (
        <Tile
          id={index}
          onClick={(event) => this.props.onSupplierClick(event)}
          onAdd={(e) => this.props.onAdd(e)}
          type={this.props.catalogType}
          price={data.price}
          unit={data.unit}
          title={data.title}
          supplier={data.supplier}
          image={data.image}
          available={data.stock}
          width='200px'
          textColor="white"
          />
      );
    });

    //jsx for rows of table view of catalog
    let catalogRows = data.map((data, index) => {
      return (
        <tr>
          <td>{data.title}</td>
          <td>{data.price}</td>
          <td>{data.unit}</td>
          <td>
            <img className={styles.tableImage} src={data.image}/>
            {data.supplier}
          </td>
          <td id="tableAdd">
            <input type="text" name="qty"/>
            <Button
              id={index}
              className="table"
              color={theme.blue}
              text={this.props.catalogType === 'product' ? '+' : '-'}
              onClick={(e) => this.props.onAdd(e)}
              size="small"
              type="add"
              />
          </td>
        </tr>
      );
    });

    let catalogTable =
    ( <table>
      <tr>
        <th>
          PRODUCT
        </th>
        <th>
          PRICE
        </th>
        <th>
          UNIT
        </th>
        <th>
          SUPPLIER
        </th>
        <th>
          QUANTITY
        </th>
      </tr>
      {catalogRows}
    </table>
  );

  let catalogCont = {
      width: '100%',
      height: '100%',
      display : 'flex',
      flexDirection : 'column',
      alignItems : 'center',
      justifyContent : 'center',
    }

    let catalogRow  = {
      width : '100%',
      display : 'flex',
      flexDirection : 'row',
    }

    return (
      <div>
        <SearchTools
          onGalleryClick={() => this.setState({view : 'gallery'})}
          onTableClick={() => this.setState({view : 'table'})}
          onFilterChange={(e) => this.props.onSliderChange(e)}
          productFilter={this.props.productFilter}/>
        <div style={catalogCont}>
          <div className={styles.resInfo}>
            {"Showing 15 results out of 130"}
          </div>
          <div className={styles.catalog}>
            {this.state.view === 'gallery' ? catalogTiles : catalogTable}
          </div>
        </div>
      </div>
    );
  }
}
