import React from 'react';
import ReactDOM from 'react-dom';
import styles from './styles/main.module.css';
import {SideNav} from './sidenav';
import {View} from './view';
import {SearchTools} from './search-tools';
import {Button} from './button';
import {Catalog} from './catalog';
import {Checkout} from './checkout';
import {Orders} from './orders';
import {Settings} from './settings';
import {Logout} from './logout';
import {Login} from './login';
import logo from '../images/bohiologo.png';
import arrow from '../images/arrow.svg';
import hamburger from '../images/bars-solid-white.png';
import {fakeOrder} from '../fakeDB.js';

export const theme = {
  red : '#eb4034',
  blue : '#246eb9',
  green : '#4cb944',
  gray : '#2b303a',
  black : '#0b0500',
  yellow : '#d2db1f'
}

export class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoggedIn : false,
      view : 'CATALOG',
      sideNavShowing : false,
      overlayShowing : false,
      whichOverlay : 'logout',
    }
    this.handleSignIn = this.handleSignIn.bind(this);
    this.handleNav = this.handleNav.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
    this.handleSubmitOrder = this.handleSubmitOrder.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleSend = this.handleSend.bind(this);
  }

  handleSignIn(e) {
    const credentials =  {
      username : "username",
      password : "password"
    }

    const init = {
      method : "POST",
      body : JSON.stringify(credentials)
    }
    fetch('./login/', init)
    .then((res) => {
      //code to process confirmation
      /*this.setState({isLoggedIn: true})*/
    });
    this.setState({isLoggedIn : true});
  }

  handleNav (event) {
    highlightMenuOptions(event);
    let text = event.target.innerText;
    if (text === 'LOGOUT') {
      this.setState({
        overlayShowing : true,
        whichOverlay : 'logout',
      });
    }
    else {
     this.setState({
       sideNavShowing : false,
       view : text,
     });
   }
  }

  handleToggle (event) {
    this.setState({sideNavShowing : !this.state.sideNavShowing});
  }

  handleLogout () {
    this.setState({
      isLoggedIn : !this.state.isLoggedIn
    })
  }

  handleSubmitOrder(e) {
    this.setState({
      overlayShowing : true,
      whichOverlay : 'orderComplete',
      view : 'CHECKOUT'
    });
  }

  handleSubmit(e) {
    this.setState({
      overlayShowing : true,
      whichOverlay : 'orderDetails',
    })
  }

  handleExport(e) {
    this.setState({
      overlayShowing : true,
      whichOverlay : 'orderExport',
    })
  }

  handleSend(e) {
    this.setState({
      overlayShowing : true,
      whichOverlay : 'orderSent',
    })
  }

  render () {
    //styling for main and overlay views
    let mainStyle = {
      width : '100%',
      height : '100%',
      display : 'flex',
      flexDirection : 'row'
    }

    let overlay = {
      display : this.state.overlayShowing ? 'flex' : 'none',
      flexDirection : 'column',
      justifyContent : 'center',
      alignItems : 'center',
      width : '100%',
      height : '100%',
      position : 'absolute',
      zIndex : '4',
      backgroundColor : 'rgba(0,0,0,0.8)',
      color : 'white',
      fontWeight : 'bold'
    }

    /*creates a listing of the order partitioned into suppliers,
    gets fed into orderDetails overlay*/
    let order = fakeOrder.map((order, index) => {
      return(
        <div className={styles.order}>
          <div className={styles.supplier}>
            <div class={styles.detailsLogo}>
              <img alt={index} src={order.logo}/>
            </div>
            {order.supplier}
          </div>
          <div className={styles.products}>
            {
              order.products.map((product, id) => {
                return(
                  <div className={styles.row}>
                    <div className={styles.name}>
                      {product.name + "(" + product.quantity + ")"}
                    </div>
                    <div className={styles.price}>{"$" + product.total}</div>
                  </div>
                );
              })
            }
          </div>
          <div className={styles.subtotal}>Subtotal: $46</div>
        </div>
      );
    });

    //these consts store jsx for the various overlays

    const orderDetails = (
      <div style={overlay}>
        <div className={styles.orderDetails}>
          {order}
        </div>
        <div className={styles.orderActions}>
          <button onClick={() => this.handleExport()}>EXPORT</button>
          <button onClick={() => this.handleSend()}>SEND</button>
          <button onClick={() => this.setState({overlayShowing: false})}>
            BACK
          </button>
          <div>TOTAL: $46</div>
        </div>
      </div>
    );

    const orderExport = (
      <div style={overlay}>
        <div>Export As: </div>
        <select onChange={() => this.setState({overlayShowing: false})}>
          <option>.pdf</option>
          <option>.jpg</option>
          <option>.png</option>
          <option>.xls</option>
        </select>
      </div>
    );

    const orderSent = (
      <div style={overlay}>
        <span>ORDER SENT</span>
        <span>{'We have sent a confirmation email to: @example.com'}</span>
        <button onClick={() => {this.setState({
            overlayShowing : false
          })}}>
          OK
        </button>
      </div>
    );

    const orderComplete = (
      <div style={overlay}>
        <span>ORDER SAVED</span>
        <span>{'See details in orders tab'}</span>
        <button onClick={() => {this.setState({
            overlayShowing : false
          })}}>
          OK
        </button>
      </div>
    );

    const logout = (
      <div style={overlay}>
        <span>LOGOUT?</span>
        <button onClick={() => this.handleLogout()}>yes</button>
        <button onClick={() => {this.setState({overlayShowing : false})}}>
          no</button>
      </div>
    );

    //logic for determining which overlay will be shown
    if (this.state.whichOverlay === 'orderDetails') overlay = orderDetails;

    if (this.state.whichOverlay === 'orderExport') overlay = orderExport;

    if (this.state.whichOverlay === 'orderSent') overlay = orderSent;

    if (this.state.whichOverlay === 'orderComplete') overlay = orderComplete;

    if (this.state.whichOverlay === 'logout') overlay = logout;

    if (!this.state.isLoggedIn) {
      return (
        <Login onSignIn={(e) => this.handleSignIn()}/>
      )
    }

    return (
      <div className={styles.main}>
        {overlay}
        <div
          onClick={() => this.handleToggle()}
          className={styles.sideNavToggler}>
          <div className={styles.sideNavArrow}>
          </div>
        </div>
        <div className={styles.topArea}>
          <div
            onClick={() => this.handleToggle()}
            className={styles.mobileToggler}>
            <img alt="hamburger" src={hamburger}/>
          </div>
        </div>
        <SideNav
          className={styles.navbar}
          showing={this.state.sideNavShowing}
          clickHandler={(event) => this.handleNav(event)}
          />
        <View
          content={this.state.view}
          onAdd={(e) => this.handleAddToCart(e)}
          onAddMore={() => this.setState({view : 'CATALOG'})}
          onSubmitOrder={(e) => this.handleSubmitOrder(e)}
          onViewOrder={() => this.setState({view : 'CHECKOUT'})}
          onSubmit={(e) => this.handleSubmit(e)}
          />
      </div>
    );
  }
}

//helper functions
function highlightMenuOptions(e) {
  //returns all options to white
  let optionClass = e.target.className;
  let options = document.getElementsByClassName(optionClass);
  for (var op of options) {
    op.style.color = "white";
    op.style.textShadow = "none";
  }

  //highlights selected option
  let style = e.target.style;
  style.color = theme.yellow;
  style.textShadow = '0 0 1px ' + theme.yellow;
}
